header{
    position: fixed;
    top: 0;
    left: 0;
    z-index: 3;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 1rem;
    background-color: #fff;
    box-shadow: 0px 0px 11px 0px #908e8e;
}

header h1{
    font-size: 2rem;
    font-weight: 700;
}

.darkModeBlock{
    display: flex;
    align-items: center;
    justify-content: center;
}

.darkModeBlock span{
    padding-left: 0.3rem;
}

@media (max-width: 450px) {
    header h1{
        font-size: 1rem;
    }
}